import "./App.css";
import React, { useState } from "react";
import Input from "./components/Input";
import axios from "axios";
import Result from "./components/Result";
function App() {
  const [input, setInput] = useState("");
  const [temp, setTemp] = useState("");
  const [description, setDescription] = useState("");
  const [icon, setIcon] = useState("");

  const findWeather = async () => {
    const apiKey = "7d22bf63bd8b5eb2952a4a4af37975ad";
    const unit = "metric";
    const url =
      "https://api.openweathermap.org/data/2.5/weather?q=" +
      input +
      "&appid=" +
      apiKey +
      "&units=" +
      unit;

    const response = await axios.get(url);
    const temp = response.data.main.temp;
    const weatherDescription = response.data.weather[0].description;
    const icon = await response.data.weather[0].icon;
    const imageURL = "http://openweathermap.org/img/w/" + icon + ".png";
    setTemp(temp);
    setDescription(weatherDescription);
    setIcon(imageURL);
    setInput("");
  };
  return (
    <div className="app">
      {temp === "" ? (
        <Input input={input} setInput={setInput} findWeather={findWeather} />
      ) : (
        <Result temp={temp} desc={description} icon={icon} setTemp={setTemp} />
      )}
    </div>
  );
}

export default App;
